<template>
  <ui-section-container key="manage_assignment_all" v-loading.body="loading">
    <ui-section title="">
      <ui-section-content>
        <div class="typo"><table style="width: 100%">
          <colgroup>
            <col/>
            <col/>
            <col style="width: 100px"/>
            <col style="width: 200px"/>
            <col style="width: 200px"/>
            <col style="width: 250px"/>
          </colgroup>
          <thead>
            <tr>
              <th></th>
              <th></th>
              <th></th>
              <th></th>
              <th></th>
              <th>ID</th>
            </tr>
          </thead>
          <tbody>
          <tr v-for="row in data" :key="row._id">
            <td>{{ row.name }}</td>
            <td>
              <router-link :to="{ name: 'ManageAssignmentDetail', params: { id: row._id } }"></router-link> |
              <router-link target="_blank" :to="{ name: 'ManageAssignmentSubmissions', params: { aid: row._id } }"></router-link>
            </td>
            <td>
              <el-tag v-if="row.visible" type="success"></el-tag>
              <el-tag v-if="!row.visible" type="gray"></el-tag>
            </td>
            <td>{{ row.begin_at | moment("YYYY-MM-DD HH:mm:ss") }}</td>
            <td>{{ row.end_at | moment("YYYY-MM-DD HH:mm:ss") }}</td>
            <td><code>{{ row._id }}</code></td>
          </tr>
          </tbody>
        </table></div>
      </ui-section-content>
    </ui-section>
  </ui-section-container>
</template>

<script>
import API from '@/utils/api';

export default {
  name: 'page-manage-assignment-all',
  data() {
    return {
      data: [],
      loading: false,
    };
  },
  created() {
    this.initData();
  },
  watch: {
    $route: 'initData',
  },
  methods: {
    async initData() {
      this.loading = true;
      try {
        this.data = (await API.manage.assignment.all()).data;
      } finally {
        this.loading = false;
      }
    },
  },
}
</script>



