<template>
  <el-form ref="form" :model="data" label-position="top">
    <el-row>
      <el-col :span="11">
        <el-form-item label="">
          <el-input v-model="data.order"></el-input>
        </el-form-item>
      </el-col>
      <el-col :span="2">&nbsp;</el-col>
      <el-col :span="11">
        <el-form-item label="">
          <el-switch on-text="" off-text="" v-model="data.visible"></el-switch>
        </el-form-item>
      </el-col>
    </el-row>
    <el-form-item label="">
      <el-input type="textarea" v-model="data.text" :autosize="{ minRows: 2 }" class="ui-mono"></el-input>
    </el-form-item>
    <transition-group name="transition-highlight">
      <el-form-item v-for="(question, idx) in data.questions" :key="question._id" :label="` ${idx + 1}`">
        <p>
          <span>
            ID:
            <span v-if="question.is_new">(New) {{ question._id }}</span>
            <span v-if="!question.is_new">{{ question._id }}</span>
          </span>
          <a href="javascript:;" @click="insertQuestion(idx)"></a>
          <a href="javascript:;" @click="deleteQuestion(idx)"></a>
        </p>
        <el-input type="textarea" v-model="question.text" :autosize="{ minRows: 2 }" class="ui-mono"></el-input>
      </el-form-item>
    </transition-group>
    <el-form-item>
      <el-button @click="appendQuestion()"></el-button>
      <el-button :disabled="!assignmentId" type="primary" @click="submit()"></el-button>
      <el-button v-if="data._id" @click="remove()"></el-button>
      <el-button @click="cancel()"></el-button>
    </el-form-item>
  </el-form>
</template>

<script>
import API from '@/utils/api';
import uuid from 'uuid/v4';

export default {
  name: 'page-manage-assignment-detail-problem-form',
  data() {
    return {
      data: this.value,
    };
  },
  props: {
    value: {
      type: Object,
      default() {
        return {
          order: 1,
          visible: true,
          text: '',
          questions: [],
        };
      },
    },
    assignmentId: String,
  },
  watch: {
    value: {
      handler(value) {
        this.data = value;
      },
    },
  },
  methods: {
    insertQuestion(index) {
      this.data.questions.splice(index, 0, {
        _id: uuid(),
        text: '',
        is_new: true,
      });
    },
    appendQuestion() {
      this.data.questions.push({
        _id: uuid(),
        text: '',
        is_new: true,
      });
    },
    deleteQuestion(index) {
      this.data.questions.splice(index, 1);
    },
    async submit() {
      if (!this.assignmentId) {
        return;
      }
      if (!this.data._id) {
        const resp = await API.manage.assignment.problem.create(this.assignmentId, this.data);
        this.$emit('create', resp.data);
      } else {
        const resp = await API.manage.assignment.problem.update(this.data._id, this.data);
        this.$emit('update', resp.data);
      }
    },
    cancel() {
      this.$emit('cancel');
    },
    async remove() {
      try {
        await this.$confirm('')
      } catch (ignore) {
        return;
      }
      await API.manage.assignment.problem.delete(this.data._id);
      this.$message.success('');
      this.$router.push({ name: 'ManageAssignmentDetail', params: { id: this.$route.params.id }});
    },
  },
}
</script>



